<template>
  <v-card>
    <v-card-title>
      Vaccines in cold storage per manufacturer
    </v-card-title>
    <v-card-text>
      <canvas id="VaccineMfrPieChart" width="100%" height="100%" />
    </v-card-text>
  </v-card>
</template>

<script>
import Chart from 'chart.js/auto'
import Vaccines from '../../constants/VaccineMfr'
import axios from 'axios'

export default {
  name: 'VaccineMfrPieChart',
  data: () => ({
    chartType: 'pie',
    chartdata: {
      labels: [...Vaccines],
      datasets: [{
        backgroundColor: [
          'rgb(255, 99, 132)',
          'rgb(255, 159, 64)',
          'rgb(255, 205, 86)',
          'rgb(75, 192, 192)',
          'rgb(54, 162, 235)',
          'rgb(153, 102, 255)',
          'rgb(201, 203, 207)',
          '#5c6bc0',
          '#29b6f6',
          '#ff9800',
          '#ec407a'
        ],
        data: [
          12,54,34,23,41,12,12,42,12,41,12
        ]
      }]
    },
    options: {
      plugins: {
        legend: {
          position: 'left',
        }
      }
    }
  }),

  mounted () {
    this.fetchVaccineLogs()
  },

  methods: {
    async fetchVaccineLogs () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/vaccine-logs/count-by-mfr`, config)

        this.chartdata.labels = data.map(v => v.manufacturer)
        this.chartdata.datasets[0].data = data.map(v => v.amount)
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        this.alert.isOpen = true
      }

      new Chart('VaccineMfrPieChart', {
        type: this.chartType,
        data: this.chartdata,
        options: this.options
      })
    },
  }
}
</script>
