<template>
  <v-card height="100%">
    <v-card-title>
      Vials used in the last 5 days (starting yesterday)
    </v-card-title>

    <v-divider />

    <v-card-text>
      <canvas id="DistributionInBrgyBarChart" width="100%" />
    </v-card-text>
  </v-card>
</template>

<script>
import Chart from 'chart.js/auto'
import {format, subDays, isSameDay} from 'date-fns'
import axios from 'axios'

export default {
  name: 'DistributionInBrgyBarChart',
  data: () => ({
    chartType: 'bar',
    chartdata: {
      labels: [],
      datasets: [
        {
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(255, 159, 64, 0.2)',
            'rgba(255, 205, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(54, 162, 235, 0.2)',
          ],
          borderColor: [
            'rgb(255, 99, 132)',
            'rgb(255, 159, 64)',
            'rgb(255, 205, 86)',
            'rgb(75, 192, 192)',
            'rgb(54, 162, 235)',
            'rgb(153, 102, 255)',
            'rgb(201, 203, 207)'
          ],
          borderWidth: 1,
          data: []
        }
      ]
    },
    options: {
      plugins: {
        legend: {
          display: false
        },
      },
      scales: {
        y: {
          beginAtZero: true
        }
      }
    }
  }),

  mounted () {
    this.setLabels()
    this.fetchVaccineLogs()
  },

  methods: {
    setLabels () {
      const labels = []

      for (let i = 0; i < 5; i++) {
        const date = new Date()
        const reduced = subDays(date, i+1)
        labels.push(format(reduced, 'MMM dd'))
      }

      this.chartdata.labels = labels
    },

    async fetchVaccineLogs () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/daily-reports`, config)

        this.vaccineLogs = data

        for (let i = 0; i < 5; i++) {
          const date = new Date()
          const reduced = subDays(date, (i+1))

          const vialsUsed = data.reduce((acc, report) => {
            const date = new Date(report.report_date)

            if (!isSameDay(date, reduced)) return acc

            return acc + report.vials_used
          }, 0)
          console.log(vialsUsed)
          this.chartdata.datasets[0].data.push(vialsUsed)
        }

        new Chart('DistributionInBrgyBarChart', {
          type: this.chartType,
          data: this.chartdata,
          options: this.options,
        })
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        this.alert.isOpen = true
      }
    },
  }
}
</script>
