<template>
  <v-container>
    <Breadcrumbs
      title="Reports (Admin)"
      icon="mdi-clipboard-alert"
      :items="breadcrumbsItems"
    />
    <DashboardTitle />

    <v-row class="align-stretch">
      <v-col cols="6">
        <DashboardCountTile
          iconSrc="vaccines-icon.png"
          title="Vaccines"
          subtitle="Total vaccines in storage"
          :value="totalVaccines"
        />
      </v-col>

      <v-col cols="6">
        <v-row no-gutters class="full-height">
          <v-col cols="6" class="pr-1">
            <DashboardCountTile
              title="Vials used today"
              :value="vialsUsedToday"
            />
          </v-col>

          <v-col cols="6" class="pl-1">
            <DashboardCountTile
              title="Vials used yesterday"
              :value="vialsUsedYesterday"
            />
          </v-col>
        </v-row>
      </v-col>
      
      <v-col cols="4">
        <DistributionPieChart />
      </v-col>
      <v-col cols="8">
        <DistributionInBrgyBarChart />
      </v-col>
      <v-col cols="6">
        <VaccineMfrPieChart />
      </v-col>
      <v-col cols="6">
        <v-row class="flex-column my-flex-spacer full-height pt-4">
          <v-col v-for="(item, index) in tileData" :key="`${item.value}+${index}`">
            <DashboardCountTile
              :iconSrc="item.iconSrc"
              :title="item.title"
              :subtitle="item.subtitle"
              :value="item.value"
            />
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Breadcrumbs from '../../components/admin/Breadcrumbs.vue'
import DashboardTitle from '@/components/admin/DashboardTitle.vue'
import DashboardCountTile from '../../components/admin/DashboardCountTile.vue'
import DistributionPieChart from '../../components/admin/DistributionPieChart.vue'
import DistributionInBrgyBarChart from '../../components/admin/DistributionInBrgyBarChart.vue'
import VaccineMfrPieChart from '../../components/admin/VaccineMfrPieChart.vue'
import axios from 'axios'
import { isToday, isYesterday } from 'date-fns'

export default {
  name: 'AdminReports',
  components: {
    Breadcrumbs,
    DashboardTitle,
    DashboardCountTile,
    DistributionPieChart,
    DistributionInBrgyBarChart,
    VaccineMfrPieChart,
  },
  data: () => ({
    breadcrumbsItems: [
      {
        text: 'Home',
        disabled: false,
      },
      {
        text: 'Reports',
        disabled: false,
      },
    ],

    tileData: [
      {
        iconSrc: 'users.png',
        title: 'Team A',
        subtitle: 'Vials used as reported today',
        value: 0
      },
      {
        iconSrc: 'lowest-vaccinated.png',
        title: 'Team B',
        subtitle: 'Vials used as reported today',
        value: 0,
      },
    ],

    totalVaccines: 0,
    reportsLogs: []
  }),

  computed: {
    vialsUsedToday () {
      return this.reportsLogs.reduce((acc, report) => {
        const date = new Date(report.report_date)
        if (!isToday(date)) return acc

        return acc + report.vials_used
      }, 0)
    },
    vialsUsedYesterday () {
      return this.reportsLogs.reduce((acc, report) => {
        const date = new Date(report.report_date)
        if (!isYesterday(date)) return acc

        return acc + report.vials_used
      }, 0)
    }
  },

  mounted () {
    this.fetchVaccineLogs()
    this.fetchVialsUsed()
  },

  methods: {
    async fetchVaccineLogs () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/vaccine-logs`, config)

        this.vaccineLogs = data
        this.totalVaccines = data.reduce(
          (acc, curr) => curr.transaction_type === 'incoming' ? acc + curr.amount : acc - curr.amount,
          0
        )
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }

        this.alert.isOpen = true
      }
    },

    async fetchVialsUsed() {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/daily-reports`, config)
        this.reportsLogs = data

        this.setVialsUsedTodayTeamA(data)
        this.setVialsUsedTodayTeamB(data)
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')
        }
      }
    },

    setVialsUsedTodayTeamA (reportsLogs) {
      this.tileData[0].value = reportsLogs.reduce((acc, report) => {
        const date = new Date(report.report_date)

        if (!isToday(date)) return acc
        if (!(report.team_id === 1)) return acc

        return acc + report.vials_used
      }, 0)
    },

    setVialsUsedTodayTeamB (reportsLogs) {
      this.tileData[1].value = reportsLogs.reduce((acc, report) => {
        const date = new Date(report.report_date)

        if (!isToday(date)) return acc
        if (!(report.team_id === 2)) return acc

        return acc + report.vials_used
      }, 0)
    },
  }
}
</script>

<style scoped>
.full-height {
  height: 100%;
}

.my-flex-spacer > * {
  flex: 1;
}
</style>
